export default {
  name: "ProductsListCard",
  props: {
    product: {
      type: Object,
      required: true,
    },
  },
  methods: {
    addToCartInlist() {
      this.$store.commit("cart/addProduct", {
        product: this.product,
        color: this.selectedColor,
        size: this.selectedSize,
        price: this.product.price,
      });
      // alert('Товар добавлен в корзину')
    },
  },
};
