export default {
  name: 'ProductGallery',
  props: {
    product: {
      type: Object,
      required: true
    }
  },
  data () {
    return {
      selectedImage: ''
    }
  },
  computed: {
    activeImage () {
      if (this.selectedImage) {
        return this.selectedImage
      } else if (this.product.images) {
        return this.product.images[0]
      } else {
        return ''
      }
    }
  },
  methods: {
    showImage (img) {
      this.selectedImage = img
    }
  }
}
